import axios from 'axios'
// import { toast } from 'react-toastify'

export const validatePhoneNumber = (data) => {
  console.log('mobile number', data)
  return async (dispatch) => {
    dispatch({ type: 'VALIDATING_MOBILE_NUMBER' })
    await axios
      .post('/backendapi/newuser/validate/mobile', data, {
        headers: {
          Authorization: 'Bearer ' + localStorage.getItem('authtoken')
        }
      })
      .then((response) => {
        //OTP received
        dispatch({ type: 'VALIDATED_MOBILE_NUMBER', data: response.data })
      })
      .catch((err) => {
        console.log('Error in checking phone number api', err) //eslint-disable-line
        dispatch({ type: 'INVALID_MOBILE_NUMBER', data: err?.response })
      })
  }
}

