import React, { useState, useEffect } from 'react'
import countryCodes from './country_codes'
import ReactCountryFlag from 'react-country-flag'
import {
  validatePhoneNumber,
  validateOTP,
  resendOTP
} from '../../redux/actions/auth/phoneValidate'
import { connect } from 'react-redux'
import themeConfig from '../../configs/themeConfig'
import { FormGroup, Input, Col, Row, Button, Label } from 'reactstrap'
import handleKeyMobileNumber from '../../utility/context/InputTypeNum'
import Select from 'react-select'
const MobileInputLogin = (props) => {
  const {
    mobile,
    setmobile,
    setvalidMobile,
    otpValidated,
    mobileValidated,
    validateOTP,
    resendOTP,
    validatePhoneNumber
  } = props
  const [selectedItem, setSelectedItem] = useState({
    countryCode: '91',
    id: 'IN',
    flag: '🇮🇳',
    mobileNumberRegex:
      '(?:61279|7(?:887[02-9]|9(?:313|79[07-9]))|8(?:079[04-9]|(?:84|91)7[02-8]))\\d{5}|(?:6(?:12|[2-47]1|5[17]|6[13]|80)[0189]|7(?:1(?:2[0189]|9[0-5])|2(?:[14][017-9]|8[0-59])|3(?:2[5-8]|[34][017-9]|9[016-9])|4(?:1[015-9]|[29][89]|39|8[389])|5(?:[15][017-9]|2[04-9]|9[7-9])|6(?:0[0-47]|1[0-257-9]|2[0-4]|3[19]|5[4589])|70[0289]|88[089]|97[02-8])|8(?:0(?:6[67]|7[02-8])|70[017-9]|84[01489]|91[0-289]))\\d{6}|(?:7(?:31|4[47])|8(?:16|2[014]|3[126]|6[136]|7[78]|83))(?:[0189]\\d|7[02-8])\\d{5}|(?:6(?:[09]\\d|1[04679]|2[03689]|3[05-9]|4[0489]|50|6[069]|7[07]|8[7-9])|7(?:0\\d|2[0235-79]|3[05-8]|40|5[0346-8]|6[6-9]|7[1-9]|8[0-79]|9[089])|8(?:0[01589]|1[0-57-9]|2[235-9]|3[03-57-9]|[45]\\d|6[02457-9]|7[1-69]|8[0-25-9]|9[02-9])|9\\d\\d)\\d{7}|(?:6(?:(?:1[1358]|2[2457]|3[2-4]|4[235-7]|5[2-689]|6[24578]|8[124-6])\\d|7(?:[235689]\\d|4[0189]))|7(?:1(?:[013-8]\\d|9[6-9])|28[6-8]|3(?:2[0-49]|9[2-5])|4(?:1[2-4]|[29][0-7]|3[0-8]|[56]\\d|8[0-24-7])|5(?:2[1-3]|9[0-6])|6(?:0[5689]|2[5-9]|3[02-8]|4\\d|5[0-367])|70[13-7]|881))[0189]\\d{5}',
    name: 'India',
    label: 'India',
    value: 'India'
  })
  const [numberError, setNumberError] = useState('')
  const initialMinute = parseInt('03')
  const initialSeconds = parseInt('00')
  const intialResendOTPTime = parseInt('30')
  const [seconds, setSeconds] = useState(initialSeconds)
  const [minutes, setMinutes] = useState(initialMinute)
  const [otpResendTime, setOTPResendTime] = useState(intialResendOTPTime)
  const [resendOTPcall, setResendOTPCall] = useState(0)
  const [invalidOTP, setInvalidOTP] = useState(false)
  const [digit1, setdigit1] = useState('')
  const [digit2, setdigit2] = useState('')
  const [digit3, setdigit3] = useState('')
  const [digit4, setdigit4] = useState('')
  const [disabled, setDisabled] = useState(false)
  const [mobileData, setMobileData] = useState({
    countryCode: '91',
    phone: ''
  })
  useEffect(() => {
    let myInterval = setInterval(() => {
      if (seconds > 0) {
        setSeconds(seconds - 1)
      }
      if (seconds === 0) {
        if (minutes === 0) {
          clearInterval(myInterval)
          setDisabled(true)
        } else {
          setMinutes(minutes - 1)
          setSeconds(59)
        }
      }
      if (otpResendTime > 0) {
        setOTPResendTime(otpResendTime - 1)
      }
      if (otpResendTime === 0) {
        setResendOTPCall(1)
        setOTPResendTime(30)
      }
    }, 1000)
    return () => {
      clearInterval(myInterval)
    }
  })
  useEffect(() => {
    !otpValidated && setmobile(mobileData.phone)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [otpValidated])
  // useEffect(() => {
  //   if (!digit1 || !digit2 || !digit3 || !digit4) setDisabled(true)
  // eslint-disable-next-line react-hooks/exhaustive-deps
  // }, [digit1, digit2, digit3, digit4])
  async function handleResendOTP(e) {
    setResendOTPCall(0)
    setOTPResendTime(30)
    let AuthData = {
      _id_OTP: 'ID pertaining to previous ID'
    }
    try {
      await resendOTP(AuthData)
      clearOTP()
    } catch (e) {}
  }
  function changeHandler(e) {
    const phoneNumber = e.target?.value
    !phoneNumber && setNumberError('')
    setMobileData({
      ...mobileData,
      phone: phoneNumber
    })
  }
  function validateMobileNumberByRegex(val) {
    const selectedCountry = countryCodes.find(
      (item) => `${item.name}` === selectedItem.name
    )
    if (
      selectedCountry &&
      !new RegExp(
        `^(${selectedCountry.mobileNumberRegex})$`.split('\\\\').join('\\')
      ).test(val)
    ) {
      setNumberError('Invalid mobile number')
    } else {
      validatePhoneNumber(mobileData)
      numberError && setNumberError('')
    }
  }
  function getFieldValue(id) {
    switch (id) {
      case 'box-1':
        return digit1
      case 'box-2':
        return digit2
      case 'box-3':
        return digit3
      case 'box-4':
        return digit4
      default:
        break
    }
  }
  function focus(e) {
    const numOfFields = 4
    const { id } = e.target
    const index = id.split('-')
    if (parseInt(index[1], 10) < numOfFields) {
      const nextSibling = document.querySelector(
        `input[id=box-${parseInt(index[1], 10) + 1}]`
      )
      if (nextSibling !== null) {
        nextSibling.focus()
      }
    }
  }
  function detectBackspace(e) {
    alert('pressed', e, e?.key, e?.keyCode, e?.which)
    var key = e?.keyCode || e?.which
    const id = e?.target?.id?.split('-')
    if (e.key !== undefined) {
      if (e.key === 'e' || e.key === '.' || e.key === '+' || e.key === '-')
        e.preventDefault()
    }
    if (key === 8 || key === 46) {
      const x = `box-${parseInt(id[1], 10)}`
      const prevSibling = document.querySelector(
        `input[id=box-${parseInt(id[1], 10) - 1}]`
      )
      if (prevSibling !== null && !getFieldValue(x)) {
        prevSibling.focus()
      }
    } else if (key < 48 || key > 57) {
      e.preventDefault()
    }
  }
  function onHandleSubmit() {
    validateMobileNumberByRegex(mobile)
  }
  function clearOTP() {
    setdigit1('')
    setdigit2('')
    setdigit3('')
    setdigit4('')
  }
  function handleChangeInput(e) {
    const { id, value } = e.target
    switch (id) {
      case 'box-1':
        setdigit1(() => value)
        break
      case 'box-2':
        setdigit2(() => value)
        break
      case 'box-3':
        setdigit3(() => value)
        break
      case 'box-4':
        !digit4 && setdigit4(() => value)
        !value && setdigit4(() => value)
        break
      default:
        break
    }
    if (value) focus(e)
  }
  function handleContinue(e) {
    e.preventDefault()
    if (!digit1 || !digit2 || !digit3 || !digit4) setInvalidOTP(true)
    const otp = [digit1, digit2, digit3, digit4].join('')
    validateOTP(otp)
  }
  return (
    <div>
      {mobileValidated ? (
        <>
          <div className="otp" style={{ display: 'flex' }}>
            <Input
              autoFocus
              type="number"
              id="box-1"
              value={digit1}
              onChange={handleChangeInput}
              onKeyDown={detectBackspace}
            />
            <Input
              type="number"
              id="box-2"
              value={digit2}
              onChange={handleChangeInput}
              onKeyDown={detectBackspace}
            />
            <Input
              type="number"
              id="box-3"
              value={digit3}
              onChange={handleChangeInput}
              onKeyDown={detectBackspace}
            />
            <Input
              type="number"
              id="box-4"
              value={digit4}
              onChange={handleChangeInput}
              onKeyDown={detectBackspace}
            />
          </div>
          <div style={{ padding: '5px', textAlign: 'center' }}>
            <div>
              {minutes === 0 && seconds === 0 ? null : (
                <span
                  style={{
                    color: 'orange',
                    fontWeight: '600'
                  }}
                >
                  {minutes}:{seconds < 10 ? `0${seconds}` : seconds}
                </span>
              )}
            </div>
            <div>{minutes === 0 && seconds < 1 && 'OTP expired!!'}</div>
            {!invalidOTP ? (
              minutes === 0 &&
              seconds > 30 && (
                <span
                  style={{
                    // cursor: resendOTPcall > 0 ? 'no-drop' : 'pointer',
                    color: resendOTPcall > 0 ? '#ff0000' : '#ff00007d',
                    pointerEvents: resendOTPcall > 0 ? '' : 'none'
                  }}
                  onClick={handleResendOTP}
                >
                  Resend OTP
                </span>
              )
            ) : (
              <div>
                <span
                  style={{
                    color: '#ff0000'
                  }}
                >
                  Invalid Otp Entered
                </span>
              </div>
            )}
          </div>

          <div style={{ padding: '5px', textAlign: 'center' }}>
            <Button.Ripple
              color="warning"
              disabled={disabled || !digit1 || !digit2 || !digit3 || !digit4}
              onClick={handleContinue}
            >
              Verify OTP
            </Button.Ripple>{' '}
          </div>
        </>
      ) : (
        <>
          <Row>
            <Col>
              <FormGroup>
                <Select
                  className="React"
                  classNamePrefix="select"
                  isClearable={true}
                  options={countryCodes}
                  name="countryCodes"
                  value={selectedItem}
                  placeholder="Select Country"
                  onChange={(e) => {
                    setSelectedItem(e)
                  }}
                />
              </FormGroup>
            </Col>
          </Row>
          {selectedItem?.countryCode && (
            <Row>
              <Col lg="2" md="2" sm="3">
                <ReactCountryFlag
                  style={{
                    width: '48px',
                    height: 'auto'
                  }}
                  countryCode={selectedItem?.id}
                  svg
                />
              </Col>
              <Col lg="3" md="3" sm="3">
                <FormGroup className="form-label-group">
                  <Input
                    value={'+' + selectedItem?.countryCode}
                    className="input-label"
                    disabled
                    id="data-price11"
                    style={{ textAlign: 'center' }}
                  ></Input>{' '}
                  <Label
                    className={
                      themeConfig.theme === 'dark'
                        ? 'dark-label'
                        : 'light-label'
                    }
                    for="data-price11"
                  >
                    Code
                  </Label>{' '}
                </FormGroup>
              </Col>
              <Col lg="7" md="7" sm="6">
                <FormGroup className="form-label-group">
                  <Input
                    className="input-label"
                    type="tel"
                    value={mobile}
                    name="phone"
                    onChange={changeHandler}
                    onKeyPress={(e) => handleKeyMobileNumber(e)}
                    id="data-price1"
                    placeholder="Enter Mobile No*"
                  />
                  <Label
                    className={
                      themeConfig.theme === 'dark'
                        ? 'dark-label'
                        : 'light-label'
                    }
                    for="data-price1"
                  >
                    Mobile No
                  </Label>
                  {numberError && (
                    <span style={{ color: 'red', fontSize: '0.8rem' }}>
                      {numberError}
                    </span>
                  )}
                </FormGroup>
              </Col>
            </Row>
          )}
          <div
            className={window.screen.width <= 500 ? 'form p-0 mt-1' : 'form'}
          >
            <Button.Ripple
              color="warning"
              disabled={!mobile}
              onClick={onHandleSubmit}
            >
              Send OTP
            </Button.Ripple>{' '}
          </div>
        </>
      )}
    </div>
  )
}
function mapStateToProps(state) {
  const { otpValidated, mobileValidated } = state
  return { otpValidated, mobileValidated: true }
}
export default connect(mapStateToProps, {
  validatePhoneNumber,
  validateOTP,
  resendOTP
})(MobileInputLogin)
